import styled from 'styled-components';

export const StyledThickButton = styled.button`
  background-color: black;
  color: white;
  text-transform: uppercase;
  border: none;
  width: 160px;
  height: 50px;
  letter-spacing: 0.2rem;
  font-weight: 600;
  margin: 30px 0;
  cursor: pointer;
`;
